@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@100&family=Quattrocento&family=Satisfy&display=swap');

#header {
    background-color: #CE2829;
    margin-top: -34px;
}

#header h2 {
    text-align: center;
    color: white;
    font-family: 'Quattrocento', sans-serif;
    font-size: 40px;
    padding: 20px;
}

.product {
    margin: 100px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.product img {
    width: 100%;
    height: auto;
    max-height: 500px;
    object-fit: contain;
    margin-bottom: 50px;
}

.product-details {
    font-size: 1.3em;
}

.explanation {
    color: #5F5F5F;
    padding-left: 30rem;
    padding-right: 30rem;
    margin: 0 auto;
}

.price-puan {
    display: flex;
    justify-content: center;
}

.product-details h1 {
    margin-top: 15px;
    font-size: 1em;
    margin-bottom: 30px;
}

#price {
    font-size: 1.3em;
    font-weight: bold;
    padding-right: 30px;
}

#puan {
    font-size: 15px;
    color: #5F5F5F;
    padding-left: 150px;
    padding-top: 20px;
}

#satisAdedi {
    font-size: 15px;
    color: #5F5F5F;
    padding-left: 50px;
    padding-top: 20px;
}
